			border-color: transparent
}
.v-select .vs__dropdown-toggle input:not(:disabled):not(.primary):not([type="radio"]):not([type="checkbox"]):not([type="range"]):not([type="submit"]):not([type="button"]):not([type="reset"]):not([type="color"]):not([type="file"]):not([type="image"]):hover, .v-select .vs__dropdown-toggle input:not(:disabled):not(.primary):not([type="radio"]):not([type="checkbox"]):not([type="range"]):not([type="submit"]):not([type="button"]):not([type="reset"]):not([type="color"]):not([type="file"]):not([type="image"]):active {
