
#app-guests .modal-container {
	overflow: visible;
}
.guest_model_content {

	--modal-width: 500px;
	--modal-height: 360px;
	--modal-gutter: 15px;

	width: var(--modal-width);

	border-radius: 3px;
	font-size: 100%;
	min-width: 200px;
	margin: var(--modal-gutter);
}
.guest_model_content .model-title,
	.guest_model_content .model-body {
		margin-bottom: var(--modal-gutter) *2;
}
.guest_model_content .form-group {
		margin: var(--modal-gutter) / 2 0;
}
.guest_model_content .form-label,
	.guest_model_content .form-input {
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		display: block;
		width: 100%;
		-webkit-transition: background .333s ease-out,
		border .333s ease-out;
		transition: background .333s ease-out,
		border .333s ease-out
}
.guest_model_content .form-label._error, .guest_model_content .form-input._error {
	border: 1px solid var(--color-error);
	color: var(--color-error);
}
.guest_model_content .vs__dropdown-menu {
		z-index: 99999;
}

